import './styles/main.css';

async function loadPartials() {
  const loads = document.querySelectorAll('load');
  const baseUrl = import.meta.env.BASE_URL;

  for (const el of loads) {
    let src = el.getAttribute('src');
    if (src) {
      let fetchUrl = src;
      if (import.meta.env.PROD && baseUrl && src.startsWith('/')) {
        // baseUrl ends with /, src starts with /, so remove one
        fetchUrl = baseUrl + src.substring(1);
      }
      
      try {
        const res = await fetch(fetchUrl);
        if (res.ok) {
          const html = await res.text();
          const wrapper = document.createElement('div');
          wrapper.innerHTML = html;
          el.replaceWith(...wrapper.children);
        } else {
          console.error('Partial could not be loaded:', fetchUrl);
        }
      } catch (err) {
        console.error('Error loading partial:', fetchUrl, err);
      }
    }
  }
}

loadPartials().then(() => {
  import('./js/index.js');
});